import React from 'react'
import './Landing.css'
import { useHistory } from 'react-router-dom'
import Post from './post/Post'

function Landing() {
    const history = useHistory()

    const submit = (e) => {
        e.preventDefault()
        history.push('/login')
    }

    return (
        <div className='landing-container'>
            <div className="hero">
                <div className="hero-content">
                    <div className="text">
                        <h1>
                            Unlimited movies, <br /> TV shows and more.
                        </h1>
                    </div>
                </div>
                <div className="input">
                    <form action="" onSubmit={submit}>
                        <input type="Email-Address" className='email-input' placeholder="email" />
                        <input type="submit"
                            className="submit"
                            value="Get started" />
                    </form>
                </div>
            </div>

            <div className="posts">
                <Post
                    title="Enjoy on your TV."
                    subtitle="Watch on smart TVs, PlayStation, Xbox, Chromecast, Apple TV, Blu-ray players and more."
                    img="https://assets.nflxext.com/ffe/siteui/acquisition/ourStory/fuji/desktop/tv.png"
                />

                <Post
                    title="Download your shows to watch offline."
                    subtitle="Save your favourites easily and always have something to watch."
                    img="https://assets.nflxext.com/ffe/siteui/acquisition/ourStory/fuji/desktop/mobile-0819.jpg"
                />

                <Post
                    title="Watch everywhere."
                    subtitle="Stream unlimited movies and TV shows on your phone, tablet, laptop, and TV."
                    img="https://assets.nflxext.com/ffe/siteui/acquisition/ourStory/fuji/desktop/device-pile-in.png"
                />

            </div>
        </div>
    )
}

export default Landing
