import React from 'react'
import './post.css'

function Post(props) {
    return (
        <div className="post-container">
            <div className="left">
                <div className="text">
                    <h1>{props.title}</h1>
                    <h3>{props.subtitle}</h3>
                </div>
            </div>
            <div className="right">
                <img src={props.img} alt="" />
            </div>
        </div>
    )
}

export default Post
